
















































p.weui_grid_label span {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

* {
  tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-tap-highlight-color: rgba(0, 0, 0, 0);
}

.timePlug {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  line-height: 1;
  z-index: 9999;
}

.timePlug > div {
  width: 90%;
  background: #fff;
  border-radius: 12px;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.timePlug > div h2 {
  font-size: 20px;
  text-align: center;
  margin-top: 10px;
}

.timePlug > div p {
  font-size: 16px;
  color: #666;
  margin-top: 10px;
  padding: 0 10px;
}

.timePlug > div .but {
  display: -webkit-box;
  font-size: 16px;
  margin-top: 10px;
  border-top: solid 1px #d1d1d1;
}

.timePlug > div .but div {
  -webkit-box-flex: 1;
  text-align: center;
  height: 40px;
  line-height: 40px;
}

.timePlug > div .time {
  height: 160px;
  width: 100%;
  overflow: hidden;
  margin-top: 10px;
  position: relative;
}

.timePlug > div .time-border {
  width: 100%;
  height: 40px;
  border-top: solid 1px #d1d1d1;
  border-bottom: solid 1px #d1d1d1;
  position: absolute;
  top: 60px;
  left: 0;
  box-sizing: border-box;
}

.swiper-container {
  width: 50%;
  float: left;
}

.swiper-slide {
  background: none;
  color: #a6a6a6;
}

.swiper-slide-active {
  color: #232323;
  font-size: 26px;
}



